/* @apply bg-unitermi-primary-gray */
body {
  box-sizing: border-box; /* Padding and Margin are no longer part of the width and height of the element */
  overflow-x: hidden;
  min-height: 100vh;
}

.wkode-slider {
  display: flex;
  transition: transform 0.5s ease;
  /*  */
}
.wkode-slider-container {
  align-items: center;
  display: flex;
  margin: auto;
  max-height: 80rem;
  overflow-x: clip;
  position: relative;
  width: 80%;
}
@media (max-width: 1080px) {
  .wkode-slider-container {
    display: none;
  }
}
.wkode-slider-slide {
  align-items: center;
  display: flex;
  flex: 1 0 auto;
  justify-content: center;
  max-width: calc(100vw - 1rem);
  width: 100%;
}
.wkode-slider-slide img {
  height: 100%;
  object-fit: cover;
  width: 100%;
}
.wkode-slider-prev, .wkode-slider-next {
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 99rem;
  color: white;
  cursor: pointer;
  font-size: 2rem;
  height: 5rem;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 5rem;
  z-index: 1;
}
.wkode-slider-prev {
  left: 2rem;
}
.wkode-slider-next {
  right: 2rem;
}
.wkode-slider-dots {
  bottom: -2rem;
  display: flex;
  justify-content: center;
  left: 0;
  margin-top: 1rem;
  position: absolute;
}
.wkode-slider-dots .wkode-slider-dot {
  background-color: #e1e1e1;
  border-radius: 0;
  cursor: pointer;
  height: 0.5rem;
  margin-right: 1.5rem;
  width: 13rem;
}
.wkode-slider-dots .wkode-slider-dot:last-of-type {
  margin-right: 0;
}
.wkode-slider-dots .wkode-slider-dot.active {
  background-color: #000327;
}
.wkode-slider-progress {
  background-color: #ddd;
  bottom: 0;
  height: 0.5rem;
  left: 0;
  position: absolute;
  width: 100%;
}
.wkode-slider-progress .wkode-slider-progress-bar {
  background-color: #000327;
  height: 100%;
  transition: none;
  width: 0%;
}

.is-desktop-preview .wkode-slider-slide {
  max-width: 100%;
}

.wkode-slider-container-mobile {
  display: none;
  max-height: 108rem;
}
@media (max-width: 1080px) {
  .wkode-slider-container-mobile {
    display: flex;
  }
}

.wk-header {
  background-color: #1f2a44;
  color: #fff;
  font-family: "Helvetica", arial, serif;
  font-size: 1.8rem;
  font-weight: 700;
}
.wk-header-wrapper {
  align-content: center;
  align-items: center;
  display: flex;
  display: flex;
  flex-direction: row;
  gap: 6rem;
  padding: 3rem 7rem;
}
.wk-header .wk-header__menu {
  align-content: center;
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
}
.wk-header .wk-header__nav-list {
  align-content: center;
  align-items: center;
  display: flex;
  flex-direction: row;
  gap: 3rem;
  text-transform: uppercase;
}